/**
 * Using lost grid
 * @documentation http://lostgrid.org/docs.html
 */

 //@lost gutter 0px;
 @lost flexbox flex;
 @lost cycle none;
 

/*
 * Centralised container utility
 */

.u-l-container {
    margin: auto;
    max-width: $max-width;
    width: 100%;
    position: relative;

    &--row {
        lost-flex-container: row;
    }

    &--small {
        margin: auto;
        max-width: $max-width-small;
    }

    &--shallow {
        margin: auto;
        max-width: $max-width-shallow;
    }

    &--narrow {
        margin: auto;
        max-width: $max-width-narrow;
    }
}

.u-l-container--center {
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    //height: 100vh;
}

 .u-section {
     width: 100%; //lost-column: 12/12;
     overflow: hidden;
 
     &--white-bg {
        //background-color: $white;
     }
 
 }
 
 .u-column {
     margin: 0 auto;
     lost-flex-container: row;
    
     //Half width
     &--half-width {
         
         // Tablet upwards
         @include breakpoint($medium) { 
            lost-column: 1/2;

            &--left {
                lost-move: -1/2;
            }
            
            &--right {
                lost-move: 1/2;
            }

        }
    }

    //Two thirds
    &--two-thirds {
         
        // Tablet upwards
        @include breakpoint($medium) { 
           lost-column: 2/3;

           &--left {
               lost-move: -2/3;
           }
           
           &--right {
               lost-move: 2/3;
           }

       }
   }

    &--image {
        img {
            margin: auto;
            transform: translateY(100px);
        }
    }
    &--full-width {
        max-width: 100%;
        outline: solid blue 1px;
    }
    &--large {
        max-width: 90%;
        outline: solid blue 1px;
    }
    &--medium {
        //lost-center: 1280px;
        max-width: 1280px;
        outline: solid purple 1px;
    }
    &--small {
        //lost-center: 1020px;
        max-width: 1020px;
        //outline: solid Green 1px;
      //   @include media('<desktop-large') {
      //       padding-left: $spacing--medium;
      //       padding-right: $spacing--medium;
      //   }
    }
 }

 .u-column {

 }
 
 %u-tb-padding {
 
     padding: $spacing--large-x 0;
 
     @include breakpoint($medium) {
         padding: $spacing--large-xxx 0;
     }
     
     @include breakpoint($large) {
         padding: $spacing--jumbo 0;
     }
 
 
     &--medium {
         padding: $spacing--small 0;
 
         @include breakpoint($medium) {
             padding: $spacing--medium 0;
         }
     
         @include breakpoint($large) {
             padding: $spacing--large 0;
         }
 
     } 
 
 
     &--small {
 
         padding: $spacing--tiny 0;
 
         @include breakpoint($medium) {
             padding: $spacing--small 0;
         }
     
         @include breakpoint($large) {
             padding: $spacing--medium 0;
         }
 
     } 
 
 
 }

 /*
 * Apply a default from small and medium screens upwards
 */

.u-l-vertical-padding {
   padding-top: $spacing--medium;
   padding-bottom: $spacing--large;

   @include breakpoint($medium) {
       padding-top: $spacing--medium;
       padding-bottom: $spacing--jumbo;
   }

   //Small
   &--small {
       padding-top: $spacing--medium;
       padding-bottom: $spacing--medium;

       @include breakpoint($medium) {
           padding-top: $spacing--large;
           padding-bottom: $spacing--large;
       }
   }

       //Small
       &--bottom {
           //padding-top: $spacing--medium;
           padding-bottom: $spacing--medium;
   
           @include breakpoint($medium) {
               //padding-top: $spacing--large-xxx;
               padding-bottom: $spacing--large-xxx;
           }
       }
}



 /*
 * Apply a default from small and medium screens upwards
 */

.u-l-horizontal-padding {
   padding-left: $horizontal-padding-small;
   padding-right: $horizontal-padding-small;

   @include breakpoint($medium) {
       padding-left: $horizontal-padding-medium;
       padding-right: $horizontal-padding-medium;
   }
}
